@import url(https://fonts.googleapis.com/css?family=Roboto:400,500,700&display=swap);
* {
	margin: 0;
	padding: 0;
	outline: 0;
	box-sizing: border-box;
}

body {
	font: 400 14px Roboto, sans-serif;
	background: #1B2838 !important;
	-webkit-font-smoothing: antialiased;
}

input, button, textarea {
	font: 400 18px Roboto, sans-serif;
}

button {
	cursor: pointer;
}

form input {
	width: 100%;
	height: 60px;
	color: #333;
	border: 1px solid #dcdce6;
	border-radius: 8px;
	padding: 0 24px;
}

form textarea {
	width: 100%;
	height: 140px;
	color: #333;
	border: 1px solid #dcdce6;
	border-radius: 8px;
	padding: 16px 24px;
	line-height: 24px;
}

.button {
	width: 100%;
	height: 60px;
	background: #e02041;
	border: 0;
	border-radius: 8px;
	color: #fff;
	font-weight: 700;
	margin-top: 16px;
	display: inline-block;
	text-align: center;
	text-decoration: none;
	font-size: 18px;
	line-height: 60px;
	transition: ease 0.2s;
}

.button:hover {
	-webkit-filter: brightness(90%);
	        filter: brightness(90%);
}

.back-link {
	display: flex;
	align-items: center;
	margin-top: 40px;
	color: #41414d;
	font-size: 18px;
	text-decoration: none;
	font-weight: 500;
	transition: opacity 0.2s;
}

.back-link svg {
	margin-right: 8px;
}

.back-link:hover {
	opacity: 0.8;
}
	.main {
		width: 100%;
		max-width: 1280px;
		padding: 0 30px;
		margin: 20px auto;
		background-color: #1B2838;
	}

	.main h2 {
		margin-top: 80px;
		margin-bottom: 24px;
		text-align: center;
		font-size: 2.5rem;
		color: #fff;
		text-transform: uppercase;
	}

	#header
	{
		display: flex;
	}

	#header img
	{
		margin: 0 auto;
		width: 200px;
	}

	h3
	{
		font-size: 1.5rem;
	}

	.Level .level-item {
		display: flex;
		flex-direction: column;
	}

	.Level h1,
	h5 {
		margin-bottom: 0;
		text-align: center;
	}

	.Level h1 {
		font-weight: 600;
		font-size: 3em;
	}

	.Level h4 {
		margin-bottom: -0.5rem;
	}

	.Level h5 {
		font-weight: 600;
		font-size: 1.2em;
		text-align: center;
		margin-bottom: 25px;
	}

	.Level .is-cherry h1, h5 { color: red; }

	.Level .is-cherry h4 {color: red;}

	.Level .is-blue h1, h5 { color: yellow; }

	.Level .is-blue h4 {color: yellow;}

	.Level .is-green h1, h5 { color: green; }

	.Level .is-green h4 {color: green;}

	.Level .is-gray h1, h5 { color: gray; }

	.Level .is-gray h4 {color: gray;} 

	#footer-bottom {
		color: #999;
	}

	#footer-bottom-inner {
		position: relative;
		padding-top: 20px;
	}

	.footer-bottom-flex {
		display: flex;
		justify-content: space-between;
		align-items: center;
	}

	#footer-bottom #copyright {
		margin-right: auto;
	}

	#footer-bottom #copyright a {
		text-decoration:none;
		color: #999;
	}
